@import 'variables';

.mainpg-container {
  #profile-container {
    display: flex;
    align-items: center;
    flex-direction: column;
    // margin: 20rem;
    // font-size: 1.5rem;
    color: white;

    // .descriptors {
    //   display: flex;
    //   align-items: center;
    //   flex-direction: column;

    //   .description {
    //     max-width: 30rem;
    //   }
    // }

    // tablet display
    @media (min-width: $breakpoint-t-min) and (max-width: $breakpoint-t-max) {
      font-size: 2rem;
      margin: 3rem;

      #profile-container {
        background-size: cover;
        display: flex;
        align-items: center;
        flex-direction: column;
        margin: 1rem;
        .description {
          max-width: fit-content;
        }
      }
    }
  }
}

// mobile display
@media (min-width: $breakpoint-m-min) and (max-width: $breakpoint-m-max) {
  #profile-container {
    // display: flex;
    // align-items: center;
    margin: 1.5rem;
    box-shadow: 0.5px 1px 10px black;
    font-size: 1.25rem;

    .descriptors {
      margin: 2.5rem;
      display: flex;
      align-items: center;
      flex-direction: column;
      .name {
        margin-bottom: 0;
      }
      p {
        margin: 0.5rem;
        font-size: 1rem;
      }
      .description {
        font-style: italic;
      }
    }
  }
}
